<template>
  <div class="search-playing" :style="{ 'background-image':`url(${SearchPlaydata.data.content.data.cover.feed})`}">

    <div class="cover">
      <div class="video" v-if="SearchPlaydata.data">
        <div class="close">
          <van-icon name="arrow-left" @click="goBack" />
        </div>
        <video :src="SearchPlaydata.data.content.data.playUrl" controls></video>
      </div>
      <div class="information">
        <div class="title">{{SearchPlaydata.data.content.data.title}}</div>
        <div class="author">
          <span class="tag">#{{SearchPlaydata.data.content.data.category}}</span> /
          <span class="author-name">{{SearchPlaydata.data.content.data.author.name}}</span>
        </div>
        <div class="description">{{SearchPlaydata.data.content.data.description}}</div>
      </div>
      <div class="push-content">
        <h3>相关推荐</h3>
        <div class="relevant-videos">
          <div class="relevant-list">
            <RankItem v-for="(item, index) in excatRelevant" :item='item' :key="index"
              @RankItemClick="RankItemClick($event)" />
            <div class="end" v-if="!excatRelevant.length">暂无更多内容</div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import RankItem from "@/components/RankItem.vue";
  export default {
    name: 'searchPlaying',
    data() {
      return {
        SearchPlaydata: null,
        excatRelevant: []
      }
    },

    created() {
      this.SearchPlaydata = this.$store.state.SearchPlaydata
      this.excatRelevant = this.getExcatRelevant()
      this.$store.commit("setShowBottomBar", false);
    },
    methods: {
       RankItemClick($event) {
        this.$store.commit('setPlayingPageDate', $event)
        this.$router.push({
          path: `/playingdetailpage?id=${$event.data.id}`
        });
      },
      getExcatRelevant() {
        let b = []
        if (this.$store.state.Relevant) {
          for (let i = 0; i < 10; i++) {
            let j = parseInt(this.getRandomInt(0, this.$store.state.Relevant.length - 1))
            this.$store.state.Relevant.map((elem, index) => {
              if (index == j) {
                b.push(elem)
              }
            })
          }
        }
        return b
      },
      getRandomInt(n, m) {
        return Math.floor(Math.random() * (m - n + 1) + n);
      },
      goBack() {
        this.$router.go(-1);
        if (!this.$store.state.isInHome) {
          this.$store.commit("setIsInHome", true);
        } else {
          this.$store.commit("setShowBottomBar", true);
        }
      }
    },
    components: {
      RankItem
    }
  }
</script>

<style lang='less' scoped>
  .search-playing {
    height: 100vh;

    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    .cover {
      width: 100%;
      height: 100%;
      padding-top: 212px;
      overflow: auto;
      background-color: rgba(0, 0, 0, .8);

      .video {
        position:fixed;
        z-index:1;
        top: 0;
        left: 0;
        width: 100%;

        .close {
          position: absolute;
          top: 0;
          left: 0;
          color: #fff;
          padding: 10px;
          font-size: 20px;
          z-index: 1;
        }

        video {
          width: 100%;
          display: block;
        }
      }

      .information {
        color: #fff;
        padding: 10px;

        .title {
          font-weight: bold;
        }

        .author,
        .description {
          font-size: 14px;
          color: #999;
          font-weight: bold;
          padding: 5px 0;
        }
      }

      .push-content {
        padding: 0 15px;
        color:#fff;

        h3 {
          color: #fafafa;
        }

        .content {
          color: #666;
        }

      }
    }

  }
</style>